import get from 'lodash/get'
import pick from 'lodash/pick'
import { useCallback, useContext, useMemo, useState } from 'react'

import { SearchContext } from '../../providers'
import { createPromptMessage, hasErrors, hydrateTree, makeIndex } from './util'
import { getBindingSlots } from '../../keymap'
import { bindTreeReducer, bindNodeReducer } from './reducers'

export default function useBindingEditor (binding, paramIndex, onUpdate) {
  const context = useContext(SearchContext)
  const { sources } = context
  const [editing, setEditing] = useState(null)

  const bindTree = useMemo(() => (
    hydrateTree(binding, context)
  ), [binding, context])

  const paramSlice = paramIndex === undefined ? [0] : [paramIndex, paramIndex + 1]

  const bind = binding.value
  const behavior = get(sources.behaviours, bind, {})
  const behaviorParams = getBindingSlots(binding, context.behaviorParameters).slice(...paramSlice)
  const bindTreeParams = bindTree.params.slice(...paramSlice)
  const index = makeIndex(bindTree)
  const invalid = useMemo(() => hasErrors(index), [index])

  const selectNodeForEditing = useCallback(({ node, type = 'value' }) => {
    const prompt = createPromptMessage(node.slot, node, type)
    setEditing({ node, type, nodeIndex: index.indexOf(node), prompt })
  }, [index, setEditing])

  const clearEditing = useCallback(() => setEditing(null), [setEditing])

  const handleUpdateBindValue = useCallback(source => {
    const { nodeIndex } = editing
    const updated = editing.type === 'tree'
      ? bindTreeReducer(bindTree, { nodeIndex, updatedNode: source })
      : bindNodeReducer(bindTree, {
        source,
        nodeIndex,
        behaviorParameters: context.behaviorParameters
      })

    onUpdate(pick(updated, ['value', 'params', '_isPlaceholder']))
    clearEditing()
  }, [bindTree, editing, clearEditing, onUpdate, context.behaviorParameters])

  const state = useMemo(
    () => ({
      behavior,
      behaviorParams,
      bindTree,
      bindTreeParams,
      editing,
      index,
      invalid
    }),
    [behavior, behaviorParams, bindTree, bindTreeParams, editing, index, invalid]
  )

  const callbacks = useMemo(
    () => ({
      clearEditing,
      handleUpdateBindValue,
      selectNodeForEditing
    }),
    [
      selectNodeForEditing,
      clearEditing,
      handleUpdateBindValue
    ]
  )

  return useMemo(() => ([state, callbacks]), [state, callbacks])
}
